import React from "react"

import Layout from "../components/layout"
import { graphql } from "gatsby"

class AboutPage extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const siteDescription = data.site.siteMetadata.description

    return (
      <Layout location={this.props.location} title={siteTitle} description={siteDescription}>
        <main>
          <h1>Hi I'm Jason</h1>
          <div>
            <p>
              I work as software engineer. I'm interested in developer effectiveness, knowledge sharing, and
              photography. This blog site lists things related to these topics.
            </p>

            <p>
              I also published a paper book and an audio book in Chinese about Software Development Effectiveness based on my experience working
              at Facebook, Microsoft and several startups. I hope you find them useful. The following are the links:
            </p>

            <p  style={{
              marginLeft: "1rem",
            }}>
              <ul>
                <li>
                  Paper: <a href={`https://shuyuan.hzmedia.com.cn/ebookdtl?id=12001315`}>
                  硅谷研发效能方法与实践（机械工业出版社华章书院）
                </a>
                </li>
                <li>
                  Audio: <a href={`https://time.geekbang.org/column/intro/222`}>
                  研发效能破局之道(极客时间专栏)
                </a>
                </li>
              </ul>
            </p>
          </div>
          <div>
            Back to the <a href={`/`}>Blog Entries</a>.
          </div>
        </main>
        <p></p>
      </Layout>
  )
  }
  }

  export default AboutPage

  export const pageQuery = graphql`
  query {
    site
    {
      siteMetadata
      {
        title
        description
      }
    }
    allMarkdownRemark(sort
  :
    {
      fields: [frontmatter___date], order
    :
      DESC
    }
  ,
    filter: {
        frontmatter: {
          enabled: {eq: "yes"}
        }
      }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            tags
          }
        }
      }
    }
  }
`
